body {
  font-family: "evolventa";
  margin: 0;
  padding: 0;
  font-size: 13px;
  // background-color: @c-6;
  background-color: #fff;
  color: #000;
}

::placeholder {
  color: @c-2;
}

::selection {
  color: #fff;
  background-color: rgba(210, 73, 73, 0.7);
}

body::-webkit-scrollbar {
  width: 1em;
}

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
}

a, a:hover, a:visited, a:active {
  text-decoration: none;
  color: #000;
  cursor: pointer;
  transition: @transition;
}

a:hover {
  color: @c-1;
}

form {
  margin: 0;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  border: none;
  background-color: transparent;
  &:focus {
    outline: none;
    box-shadow: 0;
  }
}

ul, li, ol, h1, h2, h3, h4, h5, h6, p {
  max-width: 100%;
}


svg {
  .svg-fill, .svg-stroke {
    transition: @transition;
  }
}


.white, .hover-white:hover, .hover-white.active {
  color: #fff;
  .svg-fill {
    fill: #fff;
  }
  .svg-stroke {
    stroke: #fff;
  }
}


.black, .hover-black:hover, .hover-black.active {
