.container-fluid, .like-container {
  // max-width: 1150px;
  max-width: calc( 1144px + 40px);
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  @media ( max-width: @media_tablet) {
    padding-left: 15px;
    padding-right: 15px;
  }
  @media ( max-width: @media_mobile ) {
    padding-left: 10px;
    padding-right: 10px;
  }
  &.full {
    max-width: none;
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    overflow: hidden;

    .sm-full-content {
      width: auto;
      transform: none;
    }
  }

  &.intern {
    padding-left: 0;
    padding-right: 0;
  }
}

.row {
  margin-left: -20px;
  margin-right: -20px;
  // margin: 0;

  & > * {
    padding-left: 20px;
    padding-right: 20px;
    order: 2;
  }
  @media ( max-width: @media_mobile ) {
    .mobile-1 {
      order:1!important;
    }
  }
  @media (max-width: @media_tablet) {
    // margin-left: -15px;
    // margin-right: -15px;
    margin-left: 0;
    margin-right: 0;
    & > * {
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  @media ( max-width: @media_mobile ) {
    & > * {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}

.section {
  margin-bottom: 40px;
  &:last-child {
    // margin-bottom: 0;
  }
}

.menu-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  opacity: 0;
  transition: @transition;
}

.main {
  background-color: #fff;
  max-width: 1280px;
  margin-left: auto;
  margin-right: auto;
  // overflow: hidden;
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.main-content {
  position: relative;
  padding-top: 40px;
  padding-bottom: 60px;
  // overflow: hidden;
  // width: 100%;
  @media ( max-width: @media_tablet ) {
    padding-bottom: 40px;
  }
  @media ( max-width: @media_mobile ) {
    padding-bottom: 10px;
    padding-top: 30px;
  }
}

.input {
  background-color: transparent;
  border: none;
  padding-top: 7px;
  padding-bottom: 7px;
  font-size: 13px;
  width: 100%;
  &:focus {
    outline: none;
  }
}

.input-wrap {
  border-bottom: 1px solid @c-4;
  display: flex;
  .input {
    padding-right: 10px;
  }
}

.view-pass-input {
  .view-btn {
    display: none;
  }
}

.form-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.remeber-me {
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-group {
  margin-bottom: 30px;
  position: relative;
}

.input-help {
  position: absolute;
  top: calc(100% + 2px);
  left: 0;
  width: 100%;
  opacity: 0;
  transition: .3s;
  &.visible {
    opacity: 1;
  }
  &.error {
    color: @c-1;
  }
}

.mdi-check:before {
  content: "\F12C";
}

.hamburger {
  padding: 0;
}

.hamburger-inner, .hamburger-inner:after, .hamburger-inner:before {
  width: 20px;
  height: 2px;
}

.hamburger--slider .hamburger-inner:before {
  top: 6px;
}

.hamburger--slider .hamburger-inner:after {
  top: 12px;
}

.hamburger--slider.is-active .hamburger-inner:after {
  transform: translate3d(0, -12px, 0) rotate(-90deg);
}

.hamburger-box {
  width: 20px;
  height: 16px;
}

.hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(0, 7px, 0) rotate(45deg);
}

.title {
  font-weight: bold;
}

.desktop {
  @media ( max-width: @media_tablet ) {
    display: none!important;
  }
}

.mobile {
  @media ( min-width: @media_tablet ) {
    display: none!important;
  }
  // @media ( max-width: @media_mobile ) {
  //   display: none!important;
  // }
}

.mobile-s {
  @media ( min-width: @media_mobile ) {
