  }
}

.card {
  border: none;
  border-radius: 0;
}

.shadow-right {
  &:before {
    position: absolute;
    content: '';
    display: block;
    top: 12px;
    left: 12px;
    width: 100%;
    height: 98%;
    border: 3px solid @c-8;
  }
}

.shadow-both {
  padding-left: 28px;
  padding-right: 28px;
  position: relative;
  &:before {
    position: absolute;
    content: '';
    display: block;
    top: 20px;
    left: 0;
    width: 100%;
    height: 100%;
    border: 3px solid @c-8;
  }

  &.sm-full-content {
    @media ( max-width: @media_mobile ) {
      padding: 0;
      &:before {
        display: none;
      }
    }
  }
}


.md-full-content {
  @media ( max-width: @media_tablet ) {
    width: 100%;
    width: calc(100% + 30px)!important;
    transform: translateX(-15px);
    img, > * {
      max-width: none;
      width: 100%;
    }
  }
}

.sm-full-content {
  @media ( max-width: @media_mobile ) {
    width: 100%;
    width: calc(100% + 40px)!important;
    transform: translateX(-20px);
    flex-shrink: 0;
    flex-grow: 0;
    img, > * {
      max-width: none;
      width: 100%;
    }
    display: block;
  }
}


.center-content {
  display: block;
  & > * {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
}


.sm-large-content {
  @media ( max-width: @media_mobile ) {
    width: 100%;
    img, & > * {
      width: 100%;
      max-width: none;
    }
  }
}

.slick-item {
  position: relative;
  height: auto;

  a:focus {
    outline: none!important;
  }
  img {
    width: 100%;
    height: auto;
  }
  &:focus {
    outline: none;
  }
}

.slick-dots, .slick-dots-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.slick-dots {
  list-style: none!important;
  button {
    & > * {
      display: none;
    }
    line-height: 1;
    padding: 0;
    color: transparent;
    width: 10px;
    height: 10px;
    border: 2px solid #fff;
    margin: 5px;
    border-radius: 50%;
  }
  .slick-active button {
    background-color: #fff;
  }
}

.black-dots {
  .slick-dots {
    .slick-active button {
      background-color: #000;
    }
    button {
      border-color: #000;
    }
  }
}


.slick-arrow {
  &.next-arrow {
    background-image: url('../img/icons/arr_next.svg');
  }
  &.prev-arrow {
    background-image: url('../img/icons/arr_prev.svg');
  }
}

.overlay-bottom, .overlay-top {
  position: relative;
  &:before {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.overlay-bottom:before {
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 43.05%, rgba(0, 0, 0, 0.4) 100%);
}

.overlay-top:before {
  background: linear-gradient(to top, rgba(0, 0, 0, 0) 43.05%, rgba(0, 0, 0, 0.4) 100%);
}

.dashed-shadow {
  position: relative;
  margin-bottom: 35px;
  & > * {
    position: relative;
    z-index: 2;
  }
  .shadow {
    z-index: 1;
    box-shadow: none!important;
    position: absolute;
    display: block;
    top: 18px;
    left: -18px;
    width: calc(100% + 18px * 2);
    height: 100%;
    background-image: repeating-linear-gradient(-45deg, transparent, transparent 3px, @c-8 3px, @c-8 5px, transparent 5px, transparent 8px);
    @media ( max-width: @media_mobile ) {
      top: calc(18px + 15px);
    }
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      border: 18px solid transparent;
      background-clip: content-box;
      border-top-color: #fff;
      border-left-color: #fff;

      @media ( max-width: @media_mobile ) {
        border-right: none;
      }
    }
  }

  &.shadow-space {
    .shadow {
      transform: translate(17px, 17px);
      width: calc(100% + 18px * 2 + 17px);
      left: calc(-18px - 17px);
      @media ( max-width: @media_mobile ) {
        top: 18px;
      }
    }
  }

}

.dashed-border {
  border: 18px solid transparent;
  border-left: none;
  border-right: none;
  // border-image: 16 repeating-linear-gradient(-45deg, @c-1 0, @c-1 2px, transparent 2px, transparent 3px, transparent 10px);
  border-image: 16 url('../img/icons/border_red.png');
  border-image-repeat: repeat;
  position: relative;
  &:before {
    content: '';
    position: absolute;
    top: -18px;
    left: 0;
    width: 100%;
    height: 18px;
    background-repeat: repeat;
    background-position: left center;
    background-size: auto 100%;
    background-image: url('../img/icons/border_red.png');
  }
  &:after {
    content: '';
    position: absolute;
    bottom: -18px;
    left: 0;
    width: 100%;
    height: 18px;
    background-repeat: repeat;
    background-position: left center;
    background-size: auto 100%;
    background-image: url('../img/icons/border_red.png');
  }
}

.b-left {
  border-left: 6px solid @c-1;
  padding-left: 10px;
  padding-top: .2em;
