.tabs-nav-container {
  margin-bottom: 40px;
  margin-left: 0;
  margin-right: 0;
  & > * {
    padding-left: 0;
    padding-right: 0;
  }
}

.tabs-nav {
  border-bottom: .5px solid @c-2;
  border-top: .5px solid @c-2;
  padding:0;
  display: flex;
  max-width: none;
  @media ( max-width: @media_tablet ) {
    margin-left: -15px;
    margin-right: -15px;
    padding-left: 10px;
    padding-right: 10px;
  }
  @media ( max-width: @media_mobile ) {
    margin-left: -10px;
    margin-right: -10px;
  }
  & > * {
    width: 100%;
  }
  .tabs-nav-item {
    padding: 0 20px;
    @media ( max-width: @media_mobile ) {
      padding: 0 10px;
    }
    @media ( min-width: @media_tablet ) {
      &:first-child {
        padding-left: 0;
      }
      &:last-child {
        padding-right: 0;
      }
    }

    .tabs-nav-link {
      position: relative;
      display: block;
      width: 100%;
      text-align: center;
      font-size: 20px;
      padding: 15px 0;
      @media ( max-width: @media_mobile ) {
        padding-left: 0;
        padding-right: 0;
      }
    }

    .tabs-nav-link:after, .tabs-nav-link:before {
      content: '';
      position: absolute;
      display: block;
      bottom: 0;
      height: 4px;
      background-color: @c-1;
      opacity: 0;
      transition: @transition;
    }

    .tabs-nav-link:before {
      right: 50%;
      left: 50%;
    }
    .tabs-nav-link:after {
      left: 50%;
      right: 50%;
    }

    &:hover, &.active {
      .tabs-nav-link {
        color: #000;
      }
      .tabs-nav-link:after {
        right: 0;
        opacity: 1;
      }
      .tabs-nav-link:before {
        left: 0;
        opacity: 1;
      }
    }
    &.active {
      // border-bottom-color: @c-1;
    }
  }
}



