.art-full-slider-nav {
  margin: 0;
  border: none;
  position: relative;
  @media ( max-width: @media_mobile ) {
    transform: translateX(-10px);
    width: calc(100% + 20px);
  }
  .slider-nav {
    position: absolute;
    top: 0;
    right: 60px;
    width: 130px;
    height: 100%;
    padding: 60px 0;

    @media ( max-width: @media_tablet ) {
      right: 30px;
      padding: 40px 0;
      width: 70px;
    }

    @media ( max-width: @media_mobile ) {
      position: relative;
      top: auto;
      right: auto;
      width: 100%;
      height: auto;
      padding: 20px 10px;
      padding-bottom: 0;
    }


    .slick-arrow {
      position: absolute;
      left: calc(50% - 25px / 2);

      @media ( max-width: @media_mobile ) {
        left: auto;
      }


      &.next-arrow {
        bottom: 20px;
        transform: rotate(180deg);
        @media ( max-width: @media_tablet ) {
          bottom: 10px;
        }
        @media ( max-width: @media_mobile ) {
          right: 10px;
          bottom: calc(100% + 20px);
          transform: rotate(90deg);
        }
      }
      &.prev-arrow {
        top: 20px;
        @media ( max-width: @media_tablet ) {
          top: 10px;
        }
        @media ( max-width: @media_mobile ) {
          left: 10px;
          bottom: calc(100% + 20px);
          top: auto;
          transform: rotate(-90deg);
        }
      }
    }

    .slick-list {
      height: 100%!important;
      @media ( max-width: @media_mobile ) {
        height: auto!important;
      }
    }

    .slick-item {
      margin-bottom: 25px;

      @media ( max-width: @media_tablet ) {
        margin-bottom: 10px;
      }

      @media ( max-width: @media_mobile ) {
        margin-bottom: 0;
        padding: 0 10px;
      }

      position: relative;

      &:after {
        position: absolute;
        display: block;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(255, 255, 255, 0.6);
        transition: @transition;
      }

      position: relative;
      transition: @transition;
      cursor: pointer;
      &:hover, &.slick-current {
        &:after {
          opacity: 0;
        }
      }

      &.slick-current {
        cursor: auto;
      }
    }
  }

}


