  display: inline-block;
  line-height: 2;
  cursor: pointer;
  .radio {
    content: '';
    position: absolute;
    display: block;
    width: 6px;
    height:6px;
    background-color: #000;
    top: calc(50% - 6px / 2);
    left: calc(50% - 6px / 2);
    border-radius: 50%;
    opacity: 0;
    transition: @transition;
  }
