    background-position: center center;
    background-size: 100% auto;
    background-image: url('../img/icons/check.png');
    opacity: 0;
  }

  input:checked~.check {
    opacity: 1;
  }
}


.has-checkbox {
  line-height: 1.5;
  .beauty-checkbox {
    position: absolute;
    top: 1px;
    left: 0;
  }
  position: relative;
  padding-left: 25px;
  display: block;
}




.citation {
  max-width: 550px;
  margin-left: auto;
  margin-right: auto;

  .fs-l {
    line-height: 1.25;
  }

  @media ( max-width: @media_tablet ) {
    max-width: 340px;
  }
  @media ( max-width: @media_mobile ) {
    max-width: none;
  }
}


.md-left {
  @media ( max-width: @media_tablet ) {
    text-align: left;
  }
}


figcaption {
  font-size: 13px;
  color: #000;
  margin-top: 15px;
  @media ( max-width: @media_mobile ) {
    font-size: 14px;
  }
}

figure {
  margin-bottom: 0;
}


.l-s {
  line-height: 1.5;
}


.first-letter {
  &:first-letter {
    font-size: 3em;
    font-weight: bold;
  }
}








.hover-overlay {
  &:hover:after {
    opacity: 0;
  }
  &:hover .overlay {
    opacity: 1;
    .overlay-content {
      opacity: 1;
      transform: none;
    }
  }


  .overlay {
    transition: @transition;
    background-color: rgba(0, 0, 0, 0.4);
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: bold;
    color: #fff;
    .overlay-content {
      opacity: 0;
      transition: @transition-2;
      transform: translateY(10px);
      transition-delay: .2s;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}



.sm-mobile {
  @media(min-width: @media_mobile) {
    display: none!important;
  }
}


.browser-chrome .row.list-cards-row.grid,
.browser-firefox .row.list-cards-row.grid,
// .browser-ie .row.list-cards-row.grid,
.browser-edge .row.list-cards-row.grid {
  display: grid;
  display: -ms-grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: auto;

  @media ( max-width: @media_tablet ) {
    grid-template-columns: 1fr 1fr;
  }
  @media ( max-width: @media_mobile ) {
    grid-template-columns: 1fr;
  }



  & > * {
    max-width: none;
  }


  .col-lg-12 {
    grid-column: span 3;
    @media ( max-width: @media_tablet ) {
      grid-column: span 2;
    }
    @media ( max-width: @media_mobile ) {
      grid-column: span 1;
    }
  }

  .col-lg-8 {
    grid-column: span 2;
    @media ( max-width: @media_mobile ) {
      grid-column: span 1;
    }
  }



  .col-lg-4 {
    grid-column: span 1;
    @media ( max-width: @media_tablet ) {
      grid-column: span 1;
    }
    @media ( max-width: @media_mobile ) {
      grid-column: span 1;
    }
  }


  .col-sm-6 {
    @media(max-width: @media_tablet ) {
      grid-column: span 1;
    }
    @media ( max-width: @media_mobile ) {
      grid-column: span 1;
    }
  }

  .col-sm-12 {
    @media ( max-width: @media_tablet ) {
      grid-column: span 2;
    }
    @media ( max-width: @media_mobile ) {
      grid-column: span 1;
    }
  }


  .row-banner {
    overflow: hidden;
    grid-column: span 3;
    @media( max-width: @media_tablet ) {
      grid-column: span 2;
    }
    @media ( max-width: @media_mobile ) {
      grid-column: span 1;
    }
  }


  .two-rows {
    grid-row: span 2;
  }




  &.two-cols {
    @media ( max-width: @media_mobile ) {
      grid-template-columns: 1fr 1fr;
    }
    .row-banner {
      @media(max-width: @media_mobile ) {
        grid-column: span 2;
      }
    }
  }





}
